.header{
    background-color: #3f3160;
    min-height:500px;
    width: 400px;
    margin:40px auto;
}

#to-do-form input{
    margin:20px;
    height: 50px;
    width:200px;
    background-color: rgb(27,112,137);
    border-radius: 10px ;
    padding: 0 20px;
    font-size: 18px;
    color: white;
    border:0;
}

#to-do-form input::placeholder{
   color: rgba(255,255,255,0.5);
}

#to-do-form button{
    height: 50px;
    width:80px;
    border-radius: 5px;
    border:0;
    background-color: rgb(202,156,24);
    cursor: pointer;
    font-weight: bolder;
    color: white;
}